                    {/*    marginBottom: 0, borderBottom: '2px solid #000',*/}
                    {/*    paddingBottom: '10px'*/}
                    {/*}]}>*/}
                    {/*    <View style={[styles.section, styles.flex.alignBottom, {width: '30%'}]}>*/}
                    {/*        <Text style={[styles.text.xs, {marginRight: '3px'}]}>8.3.</Text><Text>Онаси ЖШШИР:</Text>*/}
                    {/*    </View>*/}
                    {/*    <View>*/}
